/* ==========================================================================
   Spacing

   Spacing classes
========================================================================== */

// Spacing none
.spacing-inner-none {
    padding: 0;
}

.spacing-inner-top-none {
    padding-top: 0;
}

.spacing-inner-right-none {
    padding-right: 0;
}

.spacing-inner-bottom-none {
    padding-bottom: 0;
}

.spacing-inner-left-none {
    padding-left: 0;
}

.spacing-outer-none {
    margin: 0;
}

.spacing-outer-top-none {
    margin-top: 0;
}

.spacing-outer-right-none {
    margin-right: 0;
}

.spacing-outer-bottom-none {
    margin-bottom: 0;
}

.spacing-outer-left-none {
    margin-left: 0;
}

// Spacing small
.spacing-inner {
    padding: legacy.$spacing-small;
}

.spacing-inner-top {
    padding-top: legacy.$spacing-small;
}

.spacing-inner-right {
    padding-right: legacy.$spacing-small;
}

.spacing-inner-bottom {
    padding-bottom: legacy.$spacing-small;
}

.spacing-inner-left {
    padding-left: legacy.$spacing-small;
}

.spacing-inner-vertical {
    padding-bottom: legacy.$spacing-small;
    padding-top: legacy.$spacing-small;
}

.spacing-inner-horizontal {
    padding-left: legacy.$spacing-small;
    padding-right: legacy.$spacing-small;
}

.spacing-outer {
    margin: legacy.$spacing-small;
}

.spacing-outer-top {
    margin-top: legacy.$spacing-small;
}

.spacing-outer-right {
    margin-right: legacy.$spacing-small;
}

.spacing-outer-bottom {
    margin-bottom: legacy.$spacing-small;
}

.spacing-outer-left {
    margin-left: legacy.$spacing-small;
}

.spacing-outer-vertical {
    margin-bottom: legacy.$spacing-small;
    margin-top: legacy.$spacing-small;
}

.spacing-outer-horizontal {
    margin-left: legacy.$spacing-small;
    margin-right: legacy.$spacing-small;
}

// Spacing Medium
.spacing-inner-medium {
    @include get-responsive-spacing-medium(
        $type: padding,
        $direction: all,
        $is_important: false
